window.hjmobile ={};

window.hjmobile.funName = function (fun){
	var s = fun + '';
	var m = (s).match("function([ ]|\r|\n|\t)*\\w*\\(");
	var f = '';
	//alert(m);
	if(m!=undefined){
		f = m[0];
		f = f.replace(/(\t|\n| |\r|\f)+/,'');
		f = f.substring(8);
		f = f.substring(0,f.length-1);

		if(f==''){
			var tmpFunctionName = "fun" + Math.random();
			tmpFunctionName = tmpFunctionName.replace(".","_");
			var tmp = s;
			tmp = tmp.substring(8,tmp.length);
			tmp = "function " + tmpFunctionName + tmp;
			//eval(tmp);
			//alert(tmp);

			var scriptEle=document.createElement("script");
			scriptEle.type="text/javascript";
			try{
				scriptEle.appendChild(document.createTextNode(tmp));
			}catch(ex){
				scriptEle.text = tmp;
			}
			document.body.appendChild(scriptEle);
			//alert(eval(tmpFunctionName));
			return window.hjmobile.funName(eval(tmpFunctionName));
		}
	}else{
		f = s;
	}
	return f;
};

window.hjmobile.invokeObjc = function (url) {
    var iframe;
    iframe = document.createElement("iframe");
    iframe.setAttribute("src", url);
    iframe.setAttribute("width", 0);
    iframe.setAttribute("height", 0);
    iframe.setAttribute("border", 0);
    iframe.setAttribute("style", "display:none;");
    document.body.appendChild(iframe);
    iframe.parentNode.removeChild(iframe);
};

//判断当前浏览页面的系统是否为 Windows 平台
window.hjmobile.isWin=function(){
	var _isWin = (navigator.platform == "Win32") || (navigator.platform == "Windows");
	return _isWin;
};

//判断当前浏览页面的系统是否为 Android
window.hjmobile.isAndroid=function(){




	
	var _isAndroid = (window._hjmobile!=undefined) || (window._hjmobile!=null);
	return _isAndroid;
};

//判断当前浏览页面的系统是否为 iOS
window.hjmobile.isIos=function(){
	var _isIos = !window.hjmobile.isWin() && !window.hjmobile.isAndroid();
	return _isIos;
};


if(window.hjmobile.isAndroid()){
	//android
	window.hjmobile.getUUID = function(fun){
		window._hjmobile.getUUID(window.hjmobile.funName(fun));
	};

	window.hjmobile.getVersion = function(fun){
		window._hjmobile.getVersion(window.hjmobile.funName(fun));
	};

	window.hjmobile.getGPS = function(fun){
		window._hjmobile.getGPS(window.hjmobile.funName(fun));
	};

	window.hjmobile.camera = function(){
		window._hjmobile.camera();
	};

	window.hjmobile.landscape = function(){
		window._hjmobile.landscape();
	};

	window.hjmobile.portrait = function(){
		window._hjmobile.portrait();
	};

	window.hjmobile.callPhone = function(tel){
		window._hjmobile.callPhone(tel);
	};

	window.hjmobile.sendSMS = function(tel){
		window._hjmobile.sendSMS(tel);
	};

	window.hjmobile.setTag = function(tags){
		window._hjmobile.setTag(tags);
	};

	window.hjmobile.delTag = function(tags){
		window._hjmobile.delTag(tags);
	};
	
	window.hjmobile.getScanner = function(fun){
		window._hjmobile.getScanner(window.hjmobile.funName(fun));
	};
	
	window.hjmobile.scanIdCard = function(fun){
		window._hjmobile.scanIdCard(window.hjmobile.funName(fun));
	};
	window.hjmobile.scanGftId = function(fun){
		window._hjmobile.scanGftId(window.hjmobile.funName(fun));
	};

	window.hjmobile.openUrl = function(url){
		location.href=url;
	};

}else if(window.hjmobile.isWin()){
	//windows
	window.hjmobile.getUUID = function(fun){
		eval(window.hjmobile.funName(fun)+"(" + null + ");");
	};

	window.hjmobile.getVersion = function(fun){	
		eval(window.hjmobile.funName(fun)+"(" + null + ");");
	};

	window.hjmobile.getGPS = function(fun){
		eval(window.hjmobile.funName(fun)+"({\'Latitude\':28.695246,\'Longitude\':115.94903});");
	};
	
	window.hjmobile.getScanner = function(fun){
		eval(window.hjmobile.funName(fun)+"(" + null + ");");
	};
		
	window.hjmobile.scanIdCard = function(fun){
		eval(window.hjmobile.funName(fun)+"({'address':'江西省南昌市青山湖区北京东路1588号世纪风情四号地', 'idNumber':'110101199003072498', 'birthday':'19900307', 'name':'李子新', 'gender':'男', 'ethnic':'汉','photo':'https://wx4.sinaimg.cn/mw690/0071ogI5gy1gj7s0s9lg6j30iv0ivaai.jpg'});");
	};
		
	window.hjmobile.scanGftId = function(fun){
		eval(window.hjmobile.funName(fun)+"({'address':'江西省南昌市青山湖区北京东路1588号世纪风情四号地', 'idNumber':'110101199003072498', 'birthday':'19900307', 'name':'李子新', 'gender':'男', 'ethnic':'汉','photo':'https://wx4.sinaimg.cn/mw690/0071ogI5gy1gj7s0s9lg6j30iv0ivaai.jpg'});");
	};
	window.hjmobile.openUrl = function(url){
		window.open(url);
	};
}else {
	//iOS
	window.hjmobile.getUUID = function(fun){
		window.hjmobile.invokeObjc('hjmobile://getUUID?function=' + window.hjmobile.funName(fun));
	};

	window.hjmobile.getVersion = function(fun){
		window.hjmobile.invokeObjc('hjmobile://getVersion?function=' + window.hjmobile.funName(fun));
	};

	window.hjmobile.getGPS = function(fun){
		window.hjmobile.invokeObjc('hjmobile://getGPS?function=' + window.hjmobile.funName(fun));
	};

	window.hjmobile.camera = function(){
		window.hjmobile.invokeObjc('hjmobile://camera');
	};

	window.hjmobile.landscape = function(){
		window.hjmobile.invokeObjc('hjmobile://landscape');
	};

	window.hjmobile.portrait = function(){
		window.hjmobile.invokeObjc('hjmobile://portrait');
	};

	window.hjmobile.callPhone = function(tel){
		window.hjmobile.invokeObjc('hjmobile://callPhone?tel=' + tel);
	};

	window.hjmobile.sendSMS = function(tel){
		window.hjmobile.invokeObjc('hjmobile://sendSMS?tel=' + tel);
	};

	window.hjmobile.setTag = function(tags){
		window.hjmobile.invokeObjc('hjmobile://setTag?tags=' + tags);
	};

	window.hjmobile.delTag = function(tags){
		window.hjmobile.invokeObjc('hjmobile://delTag?tags=' + tags);
	};

	window.hjmobile.getScanner = function(fun){
		window.hjmobile.invokeObjc('hjmobile://scanner?function=' + window.hjmobile.funName(fun));
	};
	
	window.hjmobile.scanIdCard = function(fun){
		window._hjmobile.invokeObjc('hjmobile://scanIdCard?function=' + window.hjmobile.funName(fun));
	};
	window.hjmobile.scanGftId = function(fun){
		window._hjmobile.invokeObjc('hjmobile://scanGftId?function=' + window.hjmobile.funName(fun));
	};
	window.hjmobile.openUrl = function(url){
		window.hjmobile.invokeObjc('hjmobile://openUrl?url=' + url);
	};

}
export default window.hjmobile

