      this.certificateimg = json.photo;
      this.cardPhoto = [{ url: this.staticUrl + json.photo }]; //头像

    };
//电子身份证
      window.chop = (json) => {
      console.log(json);


      (this.form.name = json.name),
        (this.form.houseId = json.idNumber),
        (this.form.sex = json.gender == "男" ? "1" : "2"),
        (this.form.nation = json.ethnic), //民族
