       if(index==this.leiindex){
          return
         }else{
             this.cardPhoto = [];
             this.certificateimg ="";
         }

      this.leiindex= index;
    
  
     

    

    },
//初始化
    personnelDossier() {
       let params= {
         F_CheckInRecordID:this.$route.query.F_CheckInRecordID
         }
      details(params).then((res) => {

        console.log(res);

        if(res.code==0){
     
          // F_CheckInTime:"",
         

          this.form.name=res.data.F_Name,
          this.form.phone=res.data.F_MobilePhone,
          this.form.idcard=res.data.F_IDCard;
          this.form.sex =res.data.F_Sex=='男'?'1':'2',
          this.form.nation=res.data.F_Nation, //民族
          this.form.address=res.data.F_HouseholdRegisterAddress;
          if(res.data.F_IsFromLowRiskArea){this.form.current.push('0')};
          if(res.data.F_IsFromMiddleRiskArea){this.form.current.push('1')};
          if(res.data.F_IsFromHighRiskArea){this.form.current.push('2')};
          this.form.certificate=res.data.F_IDCardType==0?'身份证':res.data.F_IDCardType==1?'护照':res.data.F_IDCardType==2?'外国人永久居留证':res.data.F_IDCardType==3?'港澳通行证':'';

          this.leiindex = res.data.F_IDCardType;

          console.log(this.form.current);
          // F_IsFromLowRiskArea:(this.form.current).indexOf("0")!=-1?true:false,//是否来自低风险地区
          // F_IsFromMiddleRiskArea:(this.form.current).indexOf("1")!=-1?true:false,
          // F_IsFromHighRiskArea:(this.form.current).indexOf("2")!=-1?true:false,//是否来自高风险地区

          this.form.middle=res.data.F_MiddleRiskComeArea,//中风险来访地区
          this.form.tall=res.data.F_HighRiskComeArea,//高风险来访地区
          this.form.flow=res.data.F_TemperatureIsNormal?'1':'2',//体温是否正常
          


          this.certificateimg=res.data.F_IdentityCardPhoto,//头像
          this.form.animal=res.data.F_Temperature,//	体温
          this.identityimg=res.data.F_OwnerPersonalPhoto,//现场
          this.handimg=res.data.F_TripCode,//码
          this.form.remark=res.data.F_Remark
        

        

        this.cardPhoto = res.data.F_IdentityCardPhoto
          ? [{ url: this.staticUrl + res.data.F_IdentityCardPhoto }]
          : []; //头像
       
        this.face = res.data.F_OwnerPersonalPhoto
          ? [{ url: this.staticUrl + res.data.F_OwnerPersonalPhoto }]
          : []; //照片



        this.hand = res.data.F_TripCode
          ? [{ url: this.staticUrl + res.data.F_TripCode }]
          : []; //行程码


        }
      });
    },
    //文件删除
    deleteimg(file, detail) {
      console.log(file, detail);
       if (detail.name == "certificateimg") {
        console.log("进");
        this.certificateimg = "";
        console.log(this.certificateimg);
      } else if (detail.name == "identityimg") {
        this.identityimg = "";
        console.log(this.personal);
      } else if (detail.name == "handimg") {
        this.handimg = "";
      } 
    },
    afterRead(file, detail) {
      console.log(file, detail);
      let formData = new FormData();
      formData.append("img", file.file);
      file.message = "上传中...";
      file.status = "uploading";
      this.imgstatus = false;

      console.log(formData);

      update(formData).then((res) => {
        console.log(res);
        if (res == undefined || res == null) {
          file.message = "上传失败图片太大...";
          file.status = "failed";
          this.imgstatus = true;
        } else {
          this.imgstatus = true;
          file.status = "";
          let str = res.data.file_path;
          if (detail.name == "certificateimg") {
            console.log("进");
            this.certificateimg = str;
            console.log(this.certificateimg);
          }else if(detail.name == "handimg") {
            this.handimg = str;
          } else if (detail.name == "identityimg") {
            this.identityimg = str;
          }
          // this.uploadImages.push(str); //将图片地址存入一个数组中
          // console.log(this.uploadImages);
        }
      });
    },

    // 88888888
    onClickpersonnel() {
      console.log(1112222233333);

      if(this.isDisable) return

      
      this.isDisable=false   //点击一次时隔两秒后才能再次点击
      let identity = /^[1-9]\d{5}(18|19|([23]\d))\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{3}[0-9Xx]$/; //身份證
      let uPattern = /^[\u4E00-\u9FA5]{2,5}$/; //名字
      var telStr = /^[1](([3][0-9])|([4][5-9])|([5][0-3,5-9])|([6][5,6])|([7][0-8])|([8][0-9])|([9][1,8,9]))[0-9]{8}$/; //手机号
      let passport = /^((1[45]\d{7})|(G\d{8})|(P\d{7})|(S\d{7,8}))?$/;///护照
      let foreign =  /^[A-Z]{3}\d{6}(?:0[1-9]|1[021])(?:0[1-9]|[21]\d|3[10])\d{2}$/;
      let reg = /(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)|(^[HMhm]{1}([0-9]{10}|[0-9]{8})$)/; //港澳
      if (this.$route.query.condition != "emphasis") {

        // && this.voucherindex==0) || (!passport.test(this.form.idcard) && this.voucherindex==1) || 
        // (!foreign.test(this.form.idcard) && this.voucherindex==2) ||(!reg.test(this.form.idcard) && this.voucherindex==3)
       if (this.certificateimg == "") {
          this.$api.msg("请上传头像");
        } else 
        if (this.form.name == "" || !uPattern.test(this.form.name)) {
          this.$api.msg("请填写真实姓名");
        } else if (!identity.test(this.form.idcard)&&this.form.idcard.indexOf("*") == -1&&this.leiindex==0){
          this.$api.msg("请输入正确身份证号");
        } else if (this.form.phone == "" || !telStr.test(this.form.phone)) {
          this.$api.msg("请输入正确手机号");
         }  else if (this.form.current.length==0) {
          this.$api.msg("来访地风险等级");
         }else if (((this.form.current).indexOf("1")!=-1&&this.form.middle=="")||((this.form.current).indexOf("2")!=-1&&
          this.form.tall=="")) {
           this.$api.msg("请填写来访地"); 
        }else if (this.form.flow =='') {
           this.$api.msg("请选择体温状态"); 
        }else if (this.form.flow =='2'&&this.form.animal=="") {
           this.$api.msg("请填写体温"); 
        }else if (this.identityimg == "") {
          this.$api.msg("请上传图片");
        } else if (this.handimg=="") {
          this.$api.msg("请上传行程码");
        }else {
            // this.$api.msg("请求成功");
            this.establishDossier()
        }
        } else {

          console.log(this.form.current,this.form.current.length==0,this.form.current.length=='0');

              if (this.certificateimg == "") {
                this.$api.msg("请上传头像");
              } else 
              if (this.form.name == "" || !uPattern.test(this.form.name)) {
                this.$api.msg("请填写真实姓名");
              } else if (
                !identity.test(this.form.idcard)&&this.form.idcard.indexOf("*") == -1&&this.leiindex==0
              ) {
                this.$api.msg("请输入正确身份证号");
              } else if (this.form.phone == "" || !telStr.test(this.form.phone)) {
                this.$api.msg("请输入正确手机号");
              } else if (this.form.current.length==0) {
                this.$api.msg("来访地风险等级");
               }else if (((this.form.current).indexOf("1")!=-1&&this.form.middle=="")||((this.form.current).indexOf("2")!=-1&&
                this.form.tall=="")) {
                 this.$api.msg("请填写来访地"); 
              } else if (this.form.flow =='') {
                 this.$api.msg("请选择体温状态"); 
              }else if (this.form.flow =='2'&&this.form.animal=="") {
                 this.$api.msg("请填写体温"); 
              }else if (this.identityimg == "") {
                this.$api.msg("请上传图片");
              } else if (this.handimg=="") {
                this.$api.msg("请上传行程码");
              }else {
                  // this.$api.msg("请求成功");
                  this.modificationDossier()
        }
      }
