    //添加人员
    establishDossier() {
      let data = {};
        data = {
          F_CheckInRoom:this.$route.query.F_HotelRoomID,
          F_CheckInTime:"",

          F_IDCardType:this.leiindex,
          F_IdentityCardPhoto:this.certificateimg,
          F_Name: this.form.name,
          F_MobilePhone: this.form.phone,
          F_IDCard: this.form.idcard,
          F_Sex: this.form.sex == "1" ? "男" : "女",
          F_Nation: this.form.nation, //民族
