          F_IsFromLowRiskArea:(this.form.current).indexOf("0")!=-1?true:false,//是否来自低风险地区
          F_IsFromMiddleRiskArea:(this.form.current).indexOf("1")!=-1?true:false,
          F_IsFromHighRiskArea:(this.form.current).indexOf("2")!=-1?true:false,//是否来自高风险地区

          F_MiddleRiskComeArea:(this.form.current).indexOf("1")!=-1?this.form.middle:'',//中风险来访地区
          F_HighRiskComeArea:(this.form.current).indexOf("2")!=-1?this.form.tall:'',//高风险来访地区
          F_TemperatureIsNormal:this.form.flow=='1'?true:false,//体温是否正常
          

          F_Temperature:Number(this.form.animal),//	体温
          F_OwnerPersonalPhoto:this.identityimg,//现场
          F_TripCode:this.handimg,
          F_Remark:this.form.remark,


        };
        console.log(data);

        this.isDisable=true
           createdtraveller(data).then((res) => {
             if(res.code==0){
               this.$api.msg("入住成功");
                   let _this = this;
                  Toast({
                    message: "添加成功",
                    duration: 1000,
                    onClose() {
                      this.isDisable=false
                      _this.$router.back();
                    },
                  });
             }else{
               this.isDisable=false;
               this.$api.msg(res.msg);
             }
           })
    },
    //修改
    modificationDossier() {
      let data = {};
        data = {

          F_CheckInRecordID:this.$route.query.F_CheckInRecordID,
          F_CheckInRoom:this.$route.query.F_HotelRoomID,

          F_IDCardType:this.leiindex,
